import React from "react";
import styled from "styled-components";

import { CloseIcon } from "../../assets";

// Tab Item
export const TabItem = props => <div {...props} />;

// Tab List
const TabList = ({ children }) => <StyledTabList>{children}</StyledTabList>;

// Tab
const Tab = ({ children, ...props }) => (
  <StyledTab {...props}>{children}</StyledTab>
);

// Tab Panels
const TabPanels = ({ children }) => (
  <StyledTabPanels>{children}</StyledTabPanels>
);

// Tab Panel
const TabPanel = ({ children }) => <StyledTabPanel>{children}</StyledTabPanel>;

const Tabs = ({ children, currentTab, setCurrentTab, setTabs }) => {
  const tabs = children.map(tab => tab.props);

  const switchTab = index => setCurrentTab(index);

  const closeTab = id => {
    setTabs(tabs => tabs.filter(tab => tab.id !== id));
    setCurrentTab(id - 1);
  };

  return (
    <StyledTabs>
      <TabList>
        {tabs.map(({ label, id, locked }) => (
          <Tab onClick={() => switchTab(id)}>
            <span>{label}</span>
            {!locked && (
              <span onClick={() => closeTab(id)}>
                <CloseIcon />
              </span>
            )}
          </Tab>
        ))}
      </TabList>
      <TabPanels>
        {tabs.map(
          ({ children, id }) =>
            currentTab === id && <TabPanel>{children}</TabPanel>
        )}
      </TabPanels>
    </StyledTabs>
  );
};

export default Tabs;

const StyledTabs = styled.div`
  outline: 1px solid #000;
`;

const StyledTabList = styled.div`
  outline: 1px solid #000;
  display: flex;
  height: 32px;
`;

const StyledTab = styled.div`
  outline: 1px solid #000;
  display: flex;
  padding: 0 8px;
  cursor: pointer;
  min-width: 180px;
  align-items: center;
  justify-content: space-between;
  span:nth-child(2) {
    width: 24px;
    height: 24px;
    display: flex;
    border-radius: 50%;
    align-items: center;
    justify-content: center;
    &:hover {
      background: rgba(0, 0, 0, 0.2);
    }
  }
`;

const StyledTabPanels = styled.div`
  outline: 1px solid #000;
  height: calc(100vh - 72px);
`;
const StyledTabPanel = styled.div`
  outline: 1px solid #000;
  height: 100%;
`;
